<template>
  <div class="app-container" id="routeManagement">
    <div class="filter-container">

      <el-button class="filter-item" type="primary" @click="addList" icon="el-icon-circle-plus-outline">添加</el-button>
    </div>
    <div class="tableBox">

      <el-table v-loading="listLoading" :data="list" fit highlight-current-row :row-class-name="tableRowClassName">
        <el-table-column label="序号" prop="id" align="center" width="60" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="路线名称" min-width="150px" align="center" prop="name"></el-table-column>
        <el-table-column label="路线类型" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ formateRouteType(scope.row.schedulingType)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="路线排班日期" width="300px" align="center">
          <template slot-scope="scope">
            <span>{{ formateScheduleDates(scope.row.schedulingWeekDay)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="绑定车组" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ groupObj[scope.row.linkedGroup]}}</span>
          </template>
        </el-table-column>

        <el-table-column label="上午出车时间" min-width="180px" align="center" prop="morningTime"></el-table-column>
        <el-table-column label="下午出车时间" min-width="180px" align="center" prop="afternoonTime"></el-table-column>
        <el-table-column label="建立时间" min-width="180px" align="center" prop="createTime"></el-table-column>

        <el-table-column label="操作" align="center" min-width="330" fixed="right" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleBindRoute(row)" icon="iconfont iconsuo">绑定车组</el-button>
            <el-button type="primary" size="mini" @click="toView(row)" icon="iconfont iconchakan">查看</el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" icon="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-if="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog :visible.sync="myDial.dShow" class="myDial" :fullscreen="myDial.dFull" :modal="myDial.dModal" :show-close="myDial.dModal" :destroy-on-close="!myDial.dModal">
      <addOrEdit :geomList="geomList" :roadName="roadName" :afternoonTime="afternoonTime" :morningTime="morningTime" :schedulingType="schedulingType" :schedulingWeekDay="schedulingWeekDay" :roadId="roadId" @closePDial="closePDialF" v-if="myDial.dShow" />
    </el-dialog>
    <el-dialog id="groupDialog" title="绑定车组" :visible.sync="groupDialogFormVisible " center>
      <el-form ref="teamForm" :rules="groupRules" :model="groupTemp " label-position="left" class="formStyle">
        <el-form-item label="车组" prop="groupIds">
          <el-select filterable v-model="groupTemp.groupIds " multiple class="filter-item" placeholder="Please select">
            <el-option v-for="item in groupOptions" :key="item.id" :label="item.groupName" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="groupSubmit()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination";
import addOrEdit from './historyRoute'
// 请求的js文件
import { roadlist, deletelist, viewlist, queryBindedGroups, groupSelect,bindGroups } from '../../../api/managementArchives/routeM'
import { carGroupAlllist } from "../../../api/managementArchives/unitManagement";

import { parseToNumber, formatDate } from "../../../../src/common";

export default {
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem('userInfo')).Power,// 权限
      list: null,
      total: 0,
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10
      },
      indexMethod: 1,
      myDial: {
        styles: {// 弹窗样式
          top: '120px',
        },
        dShow: false,// 弹窗显隐
        dFull: true,// 全屏
        dModal: false,// 弹窗需要关闭的项
      },
      geomList: [],// 查看时的坐标数组
      roadName: '',// 查看时的路线名称
      morningTime: '',
      afternoonTime: '',

      roadId: null,// 查看时的路线id,
      schedulingWeekDay: '',
      schedulingType: 1,
      groupObj: {

      },
      groupDialogFormVisible: false,
      groupTemp: {},
      teamOptions: [],
      groupRules: {
        groupIds: [
          { required: true, message: "请选择车组", trigger: "change" }
        ],

      },
      groupObj:{},
      groupOptions :[]
    };
  },
  components: {
    Pagination,
    addOrEdit
  },
  created() {
    this.getList();
    this.getSelect(); //初始化弹窗的下拉框
    carGroupAlllist({}).then(res => {
      if (res.status == 0) {
        if (!res.data) {
          res.data = [];
        }
        let data = res.data;
        this.groupObj = {};
        data.forEach((item, index) => {
          this.groupObj[item.id] = item.groupName
        })
        console.warn(this.groupObj)
      } else {

        this.$message.error(res.msg);
      }

    });
  },
  methods: {
    deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteList(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
      groupSubmit() {
      this.$refs["teamForm"].validate(valid => {
        if (valid) {
          let myLoading = this.$loading({ target: "#groupDialog .el-dialog" });
          let subData = { ...this.groupTemp }
          
          bindGroups(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.routeDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      });
    },
    getSelect() {
      //路线
    
      groupSelect().then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          this.groupOptions = res.data;
          this.groupObj = {};
          this.groupOptions.forEach((item, index) => {
            this.groupObj[item.id] = item.groupName
          })
        } else {
          if (res.data == null) {
            this.groupOptions = [];
          }
          this.$message.error(res.msg);
        }


      });
    },
    queryBindedGroupsById(id) {
      queryBindedGroups({ routeId: id })
        .then(res => {
          if (res.status == 0) {
            if (!res.data) {
              res.data = [];
            }
            this.groupTemp.groupIds = res.data.map((item, index) => {
              return item.id
            });
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
        });
    },
    handleBindRoute(row) {
      this.groupDialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["teamForm"].clearValidate();
      });
      this.groupTemp = { routeId: row.id, groupIds: [] };
      this.queryBindedGroupsById(row.id);
    },
    formateRouteType(type) {
      let text = ''
      switch (type) {
        case 1:
          text = '晚收+早送'
          break;
        case 2:
          text = '晚收'
          break;

        case 3:
          text = '早送'
          break;
        case 5:
          text = '加钞'
          break;
        case 7:
          text = '收款'
          break;


      }
      return text;

    },
    formateScheduleDates(dates) {
      let arr = [];
      if (dates) {
        let datesArr = JSON.parse(dates);
        for (let i = 0; i < datesArr.length; i++) {
          switch (datesArr[i]) {
            case 1:
              arr.push('周一');
              break;
            case 2:
              arr.push('周二');
              break;
            case 3:
              arr.push('周三');
              break;
            case 4:
              arr.push('周四');
              break;
            case 5:
              arr.push('周五');
              break;
            case 6:
              arr.push('周六');
              break;
            case 7:
              arr.push('周日');
              break;
          }
        }
      } else {
        arr = [];
      }
      return arr.toString();
    },
    getList() {
      let _t = this;
      _t.listLoading = true;
      roadlist({
        page: _t.listQuery.page,
        limit: _t.listQuery.limit
      })
        .then(res => {
          if (res.status == 0) {
            _t.list = res.data.records;
            _t.list.forEach((item, index) => {
              _t.list[index].createTime = formatDate(item.createTime);
            });
            _t.total = res.data.total;
            _t.indexMethod = (_t.listQuery.page - 1) * (_t.listQuery.limit) + 1;
          } else {
            _t.$message.error(res.msg);
          };
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    searchList() {// 查询方法
      this.listQuery.page = 1;
      this.getList();
    },
    addList() {// 添加方法
      this.geomList = [];
      this.roadName = '';
      this.roadId = null;
      this.schedulingWeekDay = '',
        this.schedulingType = 1,
        this.morningTime = '';
      this.afternoonTime = '';
      this.myDial.dShow = true;
    },
    deleteList(row) {// 刪除路线
      let _t = this;
      _t.listLoading = true;
      deletelist({ geometryId: row.id }).then(res => {
        if (res.status == 0) {
          _t.$message.success('刪除成功');
          if (_t.listQuery.page > 1 && _t.list.length <= 1) {// 最后一条返回上一页
            _t.listQuery.page -= 1;
          };
          _t.getList();
        } else {
          _t.$message.error(res.msg);
          _t.listLoading = false;
        };
      }).catch(err => {
        _t.listLoading = false;
        console.error(err);
      });
    },
    closePDialF(isRefresh) {// 关闭弹窗
      this.myDial.dShow = false;
      if (isRefresh) {// 只有添加成功才刷新
        this.getList();
      };
    },
    toView(row) {
      let _t = this;
      _t.geomList = [];
      _t.roadName = row.name;
      _t.roadId = row.id;
      _t.schedulingWeekDay = row.schedulingWeekDay;
      _t.schedulingType = row.schedulingType;
      _t.morningTime = row.morningTime;
      _t.afternoonTime = row.afternoonTime;



      if (row.geometryId == null) {
        _t.geomList = []
        _t.myDial.dShow = true;
        return;
      }
      viewlist({ geometryId: row.geometryId }).then(res => {
        if (res.status == 0) {
          _t.geomList = res.data.geom.split(',');
          _t.myDial.dShow = true;
        } else {
          _t.$message.error(res.msg);
        };
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    }
  }
};
</script>
<style lang="scss">
#routeManagement {
  .myDial {
    top: 120px;
  }
}
</style>
