          </div>
          <div @click="changeLoadType(1)" :class="[loadType!=='手绘路线' ? 'isSelectClass' : 'unSelectClass']">
            <div>
              <img src="../../../../static/images/drawLineN.png" alt v-if="loadType !=='选取路线'" />
              <img src="../../../../static/images/drawLineY.png" alt v-else />
            </div>
            <span>选取路线</span>

          </div>
        </div>
        <el-form-item prop="roadName">
          <!-- <el-input placeholder="请输入路线名称" v-model="temp.roadName" :readonly="!viewHide" /> -->
          <el-input placeholder="请输入路线名称" v-model="temp.roadName" />
        </el-form-item>
        <el-form-item  class="networkItem">
          <el-select v-model="temp.network" class="filter-item networkSelect" placeholder="请选择网点" @change="selectChange" multiple filterable collapse-tags>
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-select v-model="temp.schedulingType" class="filter-item networkSelect" placeholder="路线类型" filterable collapse-tags>
            <el-option v-for="item in schedulingTypeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-select v-model="temp.schedulingWeekDay" class="filter-item networkSelect" placeholder="路线排班日期" multiple filterable collapse-tags>
            <el-option v-for="item in schedulingWeekDayOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-time-picker v-model="temp.morningTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder="上午出车时间"></el-time-picker>
        </el-form-item>
        <el-form-item class="networkItem">
          <el-time-picker v-model="temp.afternoonTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder="下午出车时间"></el-time-picker>

        </el-form-item>
        <!-- <el-form-item prop="roadName" v-show="!viewHide">
          <el-input v-model="temp.roadName" readonly="" />
        </el-form-item> -->
        <!-- <el-form-item prop="network" class="networkItem" v-show="!viewHide">
          <el-select v-model="temp.network" class="filter-item networkSelect" placeholder="请选择网点" @change="selectChange" multiple filterable collapse-tags>
            <el-option v-for="item in networkOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
          <el-form-item  class="networkItem" v-show="!viewHide">
          <el-select v-model="temp.schedulingType" class="filter-item networkSelect" placeholder="路线类型" @change="selectChange"  filterable collapse-tags>
            <el-option v-for="item in schedulingTypeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
         <el-form-item  class="networkItem" v-show="!viewHide">
          <el-select v-model="temp.schedulingWeekDay" class="filter-item networkSelect" placeholder="路线排班日期" @change="selectChange" multiple filterable collapse-tags>
            <el-option v-for="item in schedulingWeekDayOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item> -->
        <!-- <el-form-item prop="roadDiscribe">
          <el-input type="textarea" placeholder="请输入路线描述" v-model="temp.roadDiscribe "/>
        </el-form-item>-->

        <el-form-item class="btn">
          <el-button type="primary" @click="addRoute('temp')">提交</el-button>
          <el-button type="primary" @click="goBack">返回</el-button>
        </el-form-item>
      </el-form>
      <div class="editRoute">
        <el-form v-loading="lineLoading" hide-required-asterisk :inline="true" :rules="lineRule" ref="lineForm" :model="lineForm" v-show="handOrGet">
          <el-form-item prop="devDefault">
            <SelectTree :props="carprops" :options="carList" :setVal.sync="lineForm.devDefault" />
          </el-form-item>

          <el-form-item label="选择日期" prop="date">
            <el-date-picker :clearable="false" v-model="lineForm.date" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>

          <el-form-item label="开始时间" prop="timeS">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeS" value-format="HH:mm:ss" placeholder="开始时间"></el-time-picker>
            </el-col>
          </el-form-item>

          <el-form-item label="结束时间" prop="timeE">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeE" value-format="HH:mm:ss" placeholder="结束时间"></el-time-picker>
            </el-col>
          </el-form-item>

          <el-button type="primary" class="getLine" @click="getLine('lineForm')">获取路线</el-button>
        </el-form>

        <!-- <el-button v-show="showDelete" type="danger" @click="deleteRoute" >删除路线</el-button>
         <el-radio-group v-if="loadType=='手绘路线'" class="drawStart" v-model="drawStart" >
          <el-radio class="elRadio" :label="1">从起点继续绘制</el-radio>
          <el-radio class="elRadio" :label="2">从终点继续绘制</el-radio>  
        </el-radio-group>-->
      </div>
    </div>
    <!-- <el-button type="primary" @click="goBack" class="mapBtn">返回</el-button> -->
    <div class="content">
      <div id="historyRouteMap" ref="historyRouteMap"></div>
      <!-- <div class="editRoute">
        <el-form v-loading="lineLoading" hide-required-asterisk :inline="true" :rules="lineRule" ref="lineForm" :model="lineForm" v-show="handOrGet">
          <el-form-item prop="devDefault">
            <SelectTree :props="carprops" :options="carList" :setVal.sync="lineForm.devDefault" />
          </el-form-item>

          <el-form-item label="选择日期" prop="date">
            <el-date-picker :clearable="false" v-model="lineForm.date" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>

          <el-form-item label= "开始时间" prop="timeS">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeS" value-format="HH:mm:ss" placeholder="开始时间" ></el-time-picker>
            </el-col>
          </el-form-item>

          <el-form-item label="结束时间" prop="timeE">
            <el-col :span="11">
              <el-time-picker :clearable="false" v-model="lineForm.timeE" value-format="HH:mm:ss" placeholder="结束时间" ></el-time-picker>
            </el-col>
          </el-form-item>

         
          <el-button type="primary" class="getLine" @click="getLine('lineForm')">获取路线</el-button>
        </el-form>
        
        <el-button v-show="showDelete" type="danger" @click="deleteRoute" >删除路线</el-button>
         <el-radio-group v-if="loadType=='手绘路线'" class="drawStart" v-model="drawStart" >
          <el-radio class="elRadio" :label="1">从起点继续绘制</el-radio>
          <el-radio class="elRadio" :label="2">从终点继续绘制</el-radio>  
         </el-radio-group>
      </div>-->
      <div class="atMapTopBox" v-show="viewHide">
        <el-button v-show="showDelete" type="danger" @click="deleteRoute">删除路线</el-button>
        <el-radio-group v-if="loadType=='手绘路线'" class="drawStart" v-model="drawStart">
          <el-radio class="elRadio" :label="1">从起点继续绘制</el-radio>
          <el-radio class="elRadio" :label="2">从终点继续绘制</el-radio>
        </el-radio-group>
      </div>
    </div>
  </div>
</template>
<script>
import rightpng from "../../../../static/images/right.png";
import errorpng from "../../../../static/images/error.png";
import SelectTree from "../../../components/myTree/treeSelect";
import { simplify, create, update } from "../../../api/managementArchives/historyReq";
import { teamAndCarAllBindList } from "../../../api/realTimeMonitoring";
import { carList, getCarList, getCarNoList } from "../../../api/historicalPlayback";
import { Gps2Bd } from "../../../common";
import {
  allDestination, destinationByRoad
} from "../../../api/managementArchives/network";
import mapIcon from "../../../../static/images/mapicon.png";
var formatTime = function (D) {
  // 日期
  let date = new Date();
  if (D) date = new Date(D);
  let year = date.getFullYear(),
    month1 = date.getMonth() + 1,
    month = month1 > 9 ? month1 : "0" + month1,
    day1 = date.getDate(),
    day = day1 > 9 ? day1 : "0" + day1;
  return `${year}-${month}-${day}`;
};
export default {
  data() {
    var roadNameV = (rule, value, callback) => {
      // 验证是否存在路线
      if (this.temp.roadName != "") {
        // if (this.pointStrArr.length < 1) {
        //   callback(new Error("地图上未存在路线"));
        // } else {
        //   callback();
        // }
        callback();
      } else {
        callback(new Error("请输入路线名称"));
      }
    },
      networkV = (rule, value, callback) => {
        // 验证是否存在路线
        if (value.length == 0) {

          callback(new Error("请选择网点"));

        } else {
          callback();
        }
      },
      linetimeE = (rule, value, callback) => {
        let s = this.lineForm.date + " " + this.lineForm.timeS,
          e = this.lineForm.date + " " + this.lineForm.timeE;
        if (value == "" || value == null) {
          callback(new Error("请选择结束时间"));
        } else {
          if (e < s) {
            callback(new Error("结束时间小于开始时间"));
          } else {
            callback();
          }
        }
      };
    return {
      isRefresh: false,// 判断返回时是否需要刷新
      map: undefined,
      myLoading: false,
      lineLoading: false,
      carList: [], // 车辆列表
      getCarImei: {}, // 获取车牌号
      carprops: {
        // 终端下拉列表
        // 车辆下拉树配置项
        id: "id", // 传到后台的值  之前是serialNo
        label: "licenseNo" // 显示的值
      },
      loadType: "手绘路线", // 默认线路绘制模式
      handOrGet: false, // 判断是选取路线还是手绘路线,false为手绘
      showDelete: false, // 删除按钮显隐
      pointArr: [], // 海量点的原始数组
      datemin: "", // 日期最小值
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          this.datemin = minDate.getTime();
          if (maxDate) this.datemin = "";
        },
        disabledDate: now => {
          if (this.datemin != "") {
            // 禁止前后6天之外的日期
            let day6 = 1000 * 60 * 60 * 24 * 6, // 6天时间戳
              before6 = this.datemin - day6, // 前6天
              after6 = this.datemin + day6; // 后6天
            return now.getTime() < before6 || now.getTime() > after6;
          }
        }
      },
      lineForm: {
        // 路线数据
        devDefault: "", // 终端默认值
        // dateVal:'',// 日期
        date: formatTime(),
        timeS: "00:00:00",
        timeE: "23:59:59",
      },
      temp: {
        roadName: this.roadName, // 路线名
        roadDiscribe: undefined, // 路线描述
        network: [],
        schedulingWeekDay: [1, 2, 3, 4, 5],
        schedulingType: 1,
        afternoonTime: "",
        morningTime: "",
      },
      rules: {
        roadName: [{ validator: roadNameV, trigger: "blur" }],
        network: [{ validator: networkV, trigger: "change" }],

      },
      lineRule: {
        devDefault: [
          { required: true, message: "请选择终端", trigger: "change" }
        ],
        // dateVal: [
        //   { required: true, message: "请选择时间", trigger: "blur" }
        // ]
        date: [{ required: true, message: "请选择日期", trigger: "change" }],
        timeS: [
          { required: true, message: "请选择开始时间", trigger: "change" }
        ],
        timeE: [{ required: true, validator: linetimeE, trigger: "change" }]
      },
      lineOptions: {
        // 折线样式
        strokeOpacity: 1,
        strokeColor: "#3f9c0f",
        strokeWeight: 4
      },
      markerStart: new BMap.Icon(mapIcon, new BMap.Size(26, 41), {
        anchor: new BMap.Size(13, 42),
        imageOffset: new BMap.Size(-199, -139)
      }),
      markerEnd: new BMap.Icon(mapIcon, new BMap.Size(26, 41), {
        anchor: new BMap.Size(13, 42),
        imageOffset: new BMap.Size(-225, -139)
      }),
      drawStart: 2, //从起点开始绘制或终点开始绘制,默认为终点
      networkOptions: [], //网点列表

      schedulingTypeOptions: [
        { id: 1, name: '晚收+早送' },
        { id: 2, name: '晚收' },
        { id: 3, name: '早送' },
        { id: 5, name: '加钞' },
        { id: 7, name: '收款' },
        { id: 0, name: '无效' }

      ],
      schedulingWeekDayOptions: [
        { id: 1, name: '周一' },
        { id: 2, name: '周二' },
        { id: 3, name: '周三' },
        { id: 4, name: '周四' },
        { id: 5, name: '周五' },
        { id: 6, name: '周六' },
        { id: 7, name: '周日' }
      ],

      mapvLayer: null,//网点
      mapvLayer1: null,//网点

    };
  },
  props: ["geomList", 'roadName', 'roadId', 'schedulingWeekDay', 'schedulingType', 'morningTime', 'afternoonTime'],
  computed: {
    pointStrArr() {
      // 海量点原始数组转字符串数组
      return this.pointArr.map(x => `${x.lng} ${x.lat}`);
    },
    viewHide() {// 用来判断是否为查看
      return !this.roadName;
    }
  },
  components: {
    SelectTree
  },
  mounted() {

    this.initMap();
    this.getDevlist();
    this.getSelect()
  },
  methods: {
    selectChange(val) {

      if (this.mapvLayer) {
        this.mapvLayer.destroy();

      }
      if (this.mapvLayer1) {
        this.mapvLayer1.destroy();

      }
      let geom = [], geomDataNetwork = [], geomDataNetwork1 = [];

      //添加网点海量点
      for (let i = 0; i < this.temp.network.length; i++) {
        for (let j = 0; j < this.networkOptions.length; j++) {
          if (this.temp.network[i] == this.networkOptions[j].id) {

            geom = this.networkOptions[j].geom.slice(6, -1).split(" ")
            geomDataNetwork.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                },
                text: this.networkOptions[j].name
              })
            geomDataNetwork1.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                }

              })
          }
        }

      }



      let dataSet = new mapv.DataSet(geomDataNetwork);
      let dataSet1 = new mapv.DataSet(geomDataNetwork1);
      let options = {
        draw: 'text',
        avoid: true,
        size: 12,
        font: '18px Arial',
        fillStyle: '#31478F',
        shadowColor: '#31478F',
        shadowBlur: 10
      }
      this.mapvLayer = new mapv.baiduMapLayer(this.map, dataSet, options);
      let options1 = {
        fillStyle: 'rgba(255, 50, 50, 1)',
        shadowColor: 'rgba(255, 50, 50, 1)',
        shadowBlur: 30,
        globalCompositeOperation: 'lighter',
        size: 5,
        draw: 'simple'
      }
      this.mapvLayer1 = new mapv.baiduMapLayer(this.map, dataSet1, options1);

    },
    // 通过人员详情查询枪支
    getNetworkByRoute() {
      return new Promise((resolve, reject) => {
        destinationByRoad({ routeId: this.roadId })
          .then(res => {

            resolve(res.data)
          }).catch(error => {

          })
          .finally(() => {

          });
      });

    },

    //创建和初始化地图函数：
    initMap() {
      let _t = this;
      _t.createMap(); //创建地图
      _t.setMapEvent(); //设置地图事件
      _t.addMapControl(); //向地图添加控件
      _t.showDelete = false;
      _t.pointArr = []; // 删除数据
      _t.$nextTick(() => {
        _t.$refs["temp"].resetFields(); // 清空表单
      });
      // if (_t.geomList.length == 0) {
      if (_t.viewHide) {
        // 添加
        if (_t.handOrGet) {
          // 选取路线
          _t.$nextTick(() => {
            _t.$refs["lineForm"].resetFields(); // 清空表单
          });
        } else {
          _t.handDraw();
        }
      } else {
        // 查看
        let _t = this,
          map = _t.map,
          geomData = [], geomDataNetwork = [];
        this.temp.schedulingType = this.schedulingType;
        this.temp.morningTime = this.morningTime;
        this.temp.afternoonTime = this.afternoonTime;
        if (this.schedulingWeekDay) {
          // this.temp.schedulingWeekDay=JSON.parse(this.schedulingWeekDay);
          this.$set(this.temp, 'schedulingWeekDay', [...JSON.parse(this.schedulingWeekDay)])
          console.warn(this.temp.schedulingWeekDay)
        } else {
          this.temp.schedulingWeekDay = [];
          // this.$message.warning('路线存在问题，没有路线排班时间哦')

        }


        this.getNetworkByRoute().then(data => {
          if (!data) {
            data = []
          }
          //添加网点海量点
          data.forEach(x => {
            this.temp.network.push(x.id);
            let geom = x.geom.slice(6, -1).split(" ")
            geomDataNetwork.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                },
                text: x.name
              })
            // geomDataNetwork.push(new BMap.Point(geom[0],geom[1]));
          });
          let dataSet = new mapv.DataSet(geomDataNetwork);
          let options = {
            draw: 'text',
            avoid: true,
            size: 12,
            font: '18px Arial',
            fillStyle: '#31478F',
            shadowColor: '#31478F',
            shadowBlur: 10
          }
          new mapv.baiduMapLayer(map, dataSet, options)
          // let pointCollection = new BMap.PointCollection(geomDataNetwork, {
          //   color: "#d340c3",
          //   size: BMAP_POINT_SIZE_SMALL
          // });
          // map.addOverlay(pointCollection);

        })
        // .catch(err => {
        //   this.$message.error('出错啦')
        // })
        if (this.geomList.length) {
          this.geomList.forEach(x => {
            geomData.push(new BMap.Point(x.split(" ")[0], x.split(" ")[1]));
          });
          map.setViewport(geomData, { margins: [100, 100, 100, 100] });
          let polyline = new BMap.Polyline(geomData, _t.lineOptions);
          map.addOverlay(polyline);
          // 绘制海量点
          var marker1 = new BMap.Marker(geomData[0], { icon: _t.markerStart }); // 开始图标
          map.addOverlay(marker1);
          var marker2 = new BMap.Marker(geomData[geomData.length - 1], {
            icon: _t.markerEnd
          }); // 结束图标
          map.addOverlay(marker2);
          //暂时不添添加海量点
          // let pointCollection = new BMap.PointCollection(geomData, {
          //   color: "#d340c3",
          //   size: BMAP_POINT_SIZE_SMALL
          // });
          // map.addOverlay(pointCollection);
          _t.pointArr = [...geomData];
          // _t.handDraw();// 查看不需要鼠标工具
        }

      }
    },
    // 创建地图函数：
    createMap() {
      this.map = new BMap.Map("historyRouteMap", { enableMapClick: false }); //在百度地图容器中创建一个地图
      // this.map.setMapStyle({features:['road','water','land','point','building']});
      // var point = new BMap.Point(113.925973657, 22.5760535462); //定义一个中心点坐标
      // this.map.centerAndZoom(point, 13); //设定地图的中心点和坐标并将地图显示在地图容器中
      if (this.geomList.length == 0) {
        // 添加
        this.map.centerAndZoom("潮州", 13);
      }
      this.map.enableScrollWheelZoom();
    },
    //地图事件设置函数：
    setMapEvent() {
      this.map.enableDragging(); //启用地图拖拽事件，默认启用(可不写)
      this.map.enableScrollWheelZoom(); //启用地图滚轮放大缩小
      this.map.enableDoubleClickZoom(); //启用鼠标双击放大，默认启用(可不写)
      this.map.enableKeyboard(); //启用键盘上下左右键移动地图
    },
    //地图控件添加函数：
    addMapControl() {
      //向地图中添加缩放控件
      var ctrl_nav = new BMap.NavigationControl({
        anchor: BMAP_ANCHOR_TOP_LEFT,
        type: BMAP_NAVIGATION_CONTROL_LARGE
      });
      // this.map.addControl(ctrl_nav);
      //向地图中添加比例尺控件
      var ctrl_sca = new BMap.ScaleControl({ anchor: BMAP_ANCHOR_BOTTOM_LEFT });
      var overView = new BMap.OverviewMapControl();

      var mapType1 = new BMap.MapTypeControl({
        mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP],
        anchor: BMAP_ANCHOR_TOP_LEFT
      });
      // this.map.addControl(ctrl_sca);

      this.map.addControl(mapType1); //2D图，混合图
    },
    //移除地图类型和缩略图
    delete_control() {
      this.map.removeControl(mapType1); //移除2D图，混合图
      this.map.removeControl(overView);
      this.map.removeControl(overViewOpen);
    },
    // 获取历史路线
    getLine(formName) {
      let _t = this;
      _t.map.clearOverlays();
      this.selectChange();
      _t.pointArr = []; // 删除数据
      _t.$nextTick(() => {
        _t.$refs[formName].validate(valid => {
          if (valid) {
            _t.lineLoading = true;
            let s = new Date(
              _t.lineForm.date.replace(/-/g, "/") + " " + _t.lineForm.timeS
            ).getTime(),
              e = new Date(
                _t.lineForm.date.replace(/-/g, "/") + " " + _t.lineForm.timeE
              ).getTime();
            // getCarList({
            //   serialNo: _t.getCarImei[_t.lineForm.devDefault],
            //   startTime: s,
            //   endTime: e
            // })
            //改成了通过车牌号查询而不是设备号查询
            getCarNoList({
              carNo: _t.getCarImei[_t.lineForm.devDefault],
              startTime: s,
              endTime: e,
              hasAddress: false
            })
              .then(res => {
                // console.log(res)
                if (res.success) {
                  if (
                    res.data == null ||
                    res.data.length == 0 ||
                    res.data.filter(
                      x =>
                        x.lat != null &&
                        x.lng != null &&
                        x.lat * 1 > 1 &&
                        x.lng * 1 > 1
                    ).length == 0
                  ) {
                    _t.$message.warning("查询结果为空，请重新选择");
                    _t.showDelete = false; // 隐藏删除按钮
                  } else {
                    // 将数据转化为地图对象集合，赋值到pointArr中
                    // mlng 经过转换后的经度 mlat 经过转换后的纬度
                    _t.showDelete = true; // 显示删除按钮
                    let datas = [];
                    datas = res.data
                      .filter(
                        x =>
                          x.lat != null &&
                          x.lng != null &&
                          x.lat * 1 > 1 &&
                          x.lng * 1 > 1
                      )
                      .map(x => {
                        let point = Gps2Bd(new BMap.Point(x.lng, x.lat)); // 转换为百度坐标
                        return `${point.lng} ${point.lat}`;
                      });
                    // console.log('简化前数据');
                    // console.log(datas);
                    // 获取简化数据
                    let myData = { wktGps: datas };
                    simplify(myData).then(results => {
                      if (results.success) {
                        // 处理数据
                        // console.log(results);
                        let geom = results.data.slice(11, -1).split(",");
                        // console.log('简化后数据');
                        // console.log(geom);
                        geom.forEach(x => {
                          let newPointArr = x.split(" ");
                          _t.pointArr.push(
                            new BMap.Point(x.split(" ")[0], x.split(" ")[1])
                          );
                        });
                        // console.log( _t.pointArr);
                        _t.$refs.temp.validateField("roadName"); // 验证路线
                        _t.map.setViewport(_t.pointArr, {
                          margins: [100, 100, 100, 100]
                        });
                        _t.getNewPoint();
                      } else {
                        _t.$message.error(results.msg);
                      }
                    });
                  }
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.lineLoading = false;
              });
          }
        });
      });
    },
    // 添加路线
    addRoute(formName) {
      let _t = this;
      let req = create;

      _t.$nextTick(() => {
        _t.$refs[formName].validate(valid => {
          if (valid) {
            _t.myLoading = true;
            let myData;
            if (!this.viewHide) {
              req = update;
              myData = {
                id: this.roadId,
                name: _t.temp.roadName,
                // corss: JSON.stringify(_t.temp.network),
                schedulingType: _t.temp.schedulingType
                // schedulingWeekDay:JSON.stringify( _t.temp.schedulingWeekDay),
                // wktGps: _t.pointStrArr
              };
              // if (_t.temp.schedulingWeekDay.length !== 0) {
                myData.schedulingWeekDay = JSON.stringify(_t.temp.schedulingWeekDay)
              // }
                if (_t.temp.network.length) {
                myData.corss =JSON.stringify(_t.temp.network)
              }
              if (_t.temp.morningTime) {
                myData.morningTime = _t.temp.morningTime
              }
              if (_t.temp.afternoonTime) {
                myData.afternoonTime = _t.temp.afternoonTime
              }
            } else {
              myData = {
                name: _t.temp.roadName,
                // crosslobby: JSON.stringify(_t.temp.network),
                schedulingType: _t.temp.schedulingType,

                // wktGps: _t.pointStrArr
              };
               if (_t.temp.network.length) {
                myData.crosslobby =JSON.stringify(_t.temp.network)
              }
              if (_t.temp.schedulingWeekDay.length !== 0) {
                myData.schedulingWeekDay = JSON.stringify(_t.temp.schedulingWeekDay)
              }
              if (_t.pointStrArr.length) {
                myData.wktGps = _t.pointStrArr.length
              }
              if (_t.temp.morningTime !== null) {
                myData.morningTime = _t.temp.morningTime
              }
              if (_t.temp.afternoonTime !== null) {
                myData.afternoonTime = _t.temp.afternoonTime
              }

            }

            req(myData)
              .then(res => {
                if (res.status == 0) {
                  _t.isRefresh = true;
                  let text = '添加成功';
                  if (!this.viewHide) {
                    text = '编辑成功';
                  _t.$message.success(text);
                  this.goBack();
                  return;
                  }
                  _t.$message.success(text);
                  _t.initMap();
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.myLoading = false;
              });
          }
        });
      });
    },
    changeLoadType(type) {
      // 切换绘图方式
      if (type == 0) {
        this.loadType = "手绘路线";
        this.handOrGet = false;
      } else if (type == 1) {
        this.loadType = "选取路线";
        this.handOrGet = true;
      }
      // this.handOrGet = !this.handOrGet;
      this.initMap();
      if (type == 0) {
        this.handDraw();
      }
    },
    handDraw() {
      // 手绘线路
      let _t = this,
        drawingManager = new BMapLib.DrawingManager(_t.map, {
          //实例化鼠标绘制工具
          enableDrawingTool: true,
          polylineOptions: _t.lineOptions,
          drawingToolOptions: {
            drawingModes: [BMAP_DRAWING_POLYLINE]
          }
        });
      drawingManager.addEventListener("polylinecomplete", function (overlay) {
        // 绘制线路完成后
        _t.showDelete = true; // 显示删除按钮
        // _t.pointArr = overlay.getPath();// 海量点原始数组
        if (_t.drawStart == 2) {
          _t.pointArr = _t.pointArr.concat(overlay.getPath());
        } else if (_t.drawStart == 1 && _t.pointArr.length) {
          let reverseArr = [...overlay.getPath()].reverse();
          _t.pointArr = reverseArr.concat(_t.pointArr);
        } else {
          _t.pointArr = overlay.getPath();
        }
        // _t.map.removeOverlay(overlay);// 移除绘制的线
        _t.map.clearOverlays();
        _t.selectChange();
        _t.$refs.temp.validateField("roadName"); // 验证路线
        _t.getNewPoint();
        // document.getElementsByClassName('BMapLib_Drawing_panel')[0].style.display = 'none';// 隐藏绘制按钮
      });
    },
    getNewPoint() {
      // 获取最新的所有点集合
      let _t = this,
        map = _t.map,
        count = 0, // 点击次数--需要重置
        marks = [], // 点标记对象数组 [H{lng:123,lat:456}]--需要重置
        clickIndexArr = [], // 两次点击的下标数组 [1,6]，通过此数组，可以获取点标记在原始数组中的位置，方便增删--需要重置
        polyline = undefined, // 折线
        pointCollection = undefined, // 海量点
        pointCArr = [..._t.pointArr], // 海量点数组，copy原始数组，用来绘制视图上的海量点 [H{lng:123,lat:456}]--需要重置
        editLine = undefined, // 可编辑线
        editLineArr = []; // 可编辑线包含的点对象集合 [H{lng:123,lat:456}]--需要重置

      // 为了方便手动绘制与后台获取同一方法，直接重新绘制需要的线
      polyline = new BMap.Polyline(_t.pointArr, _t.lineOptions);
      map.addOverlay(polyline);
      // 绘制海量点
      var marker1 = new BMap.Marker(_t.pointArr[0], { icon: _t.markerStart }); // 开始图标
      map.addOverlay(marker1);
      var marker2 = new BMap.Marker(_t.pointArr[_t.pointArr.length - 1], {
        icon: _t.markerEnd
      }); // 结束图标
      map.addOverlay(marker2);
      pointCollection = new BMap.PointCollection(_t.pointArr, {
        color: "#d340c3",
        size: BMAP_POINT_SIZE_SMALL
      });
      map.addOverlay(pointCollection);
      // 点击海量点
      pointCollection.addEventListener("click", function (e) {
        let clickP = e.point, // 点击的坐标点对象
          clickPoint = `${clickP.lng} ${clickP.lat}`, // 点击的坐标点转字符串，方便对比定位
          clickIndex = _t.pointStrArr.indexOf(clickPoint), // 点击的坐标点在海量点中的位置
          markoption = {
            icon: new BMap.Symbol(BMap_Symbol_SHAPE_CIRCLE, {
              scale: 7,
              fillColor: "red",
              fillOpacity: 1,
              strokeWeight: 1
            })
          }; // 点标记默认样式
        ///////////////////
        if (marks.length < 2) {
          clickIndexArr.push(clickIndex); // 两次点击的下标数组，如果不是顺序排列，需要倒序
          editLineArr.push(clickP); // 两次点击的点对象数组，用于绘制编辑线
          marks[count] = new BMap.Marker(clickP, markoption); // 描绘点标记
          map.addOverlay(marks[count++]);
        }
        if (count == 2) {
          // 有两个点标记,在点击确定或者取消之前，禁止再点击
          count = 3; // 保证第二次点击之后再发生点击事件
          if (clickIndexArr[0] > clickIndexArr[1]) {
            // 顺序不对，倒序
            clickIndexArr.reverse();
            marks.reverse();
            editLineArr.reverse();
          }
          marks[0].setIcon(new BMap.Icon(rightpng, new BMap.Size(16, 16))); // 绘制确定图标
          marks[1].setIcon(new BMap.Icon(errorpng, new BMap.Size(16, 16))); // 绘制取消图标
          // 如果点处于第一个或者最后一个，需要偏移，以免挡住编辑框
          if (clickIndexArr[0] == 0) {
            marks[0].setOffset(new BMap.Size(12, 12));
          }
          if (clickIndexArr[1] == _t.pointArr.length - 1) {
            marks[1].setOffset(new BMap.Size(12, 12));
          }
          // 绘制可编辑线，此时editLineArr只有两个点
          editLine = new BMap.Polyline(editLineArr, {
            strokeColor: "red",
            enableEditing: true
          });
          map.addOverlay(editLine); // 两个下标绘制的可编辑线
          pointCArr.splice(
            clickIndexArr[0],
            clickIndexArr[1] - clickIndexArr[0] + 1
          ); // 只操作视图上的海量点，删除两个点标记包含的数据
          pointCollection.setPoints(pointCArr); // 重新绘制海量点
          // 编辑线
          editLine.addEventListener("lineupdate", function (e) {
            editLineArr = e.target.getPath(); // 此时editLineArr有多个点
          });

          marks.forEach((x, i) => {
            // 点击确定或者取消
            x.addEventListener("click", function () {
              if (i == 0) {
                // 确定，使用编辑之后的海量点
                _t.pointArr.splice(
                  clickIndexArr[0],
                  clickIndexArr[1] - clickIndexArr[0] + 1
                ); // 删除两个点标记包含的数据
                editLineArr.forEach((y, j) => {
                  _t.pointArr.splice(clickIndexArr[0] + j, 0, y); // 添加editLineArr
                });
              }
              map.removeOverlay(marks[0]); // 删除点标记
              map.removeOverlay(marks[1]);
              map.removeOverlay(editLine); // 删除可编辑线
              map.removeOverlay(polyline); // 删除初始线
              pointCollection.clear(); // 删除海量点
              count = 0; // 重新计数
              marks = [];
              clickIndexArr = [];
              editLineArr = [];
              pointCArr = [..._t.pointArr];
              polyline = new BMap.Polyline(_t.pointArr, _t.lineOptions); // 重新绘制折线
              map.addOverlay(polyline);
              pointCollection.setPoints(_t.pointArr); // 重新绘制海量点
              // console.log('最终需要的点');
              // console.log(_t.pointStrArr);
              // 重新设置起终点标记
              marker1.setPosition(_t.pointArr[0]);
              marker2.setPosition(_t.pointArr[_t.pointArr.length - 1]);
            });
          });
        }
      });
    },
    deleteRoute() {
      // 删除线路
      this.map.clearOverlays();
      this.selectChange();
      this.pointArr = [];
      this.showDelete = false;
      let BMapLib_Drawing_panel = document.getElementsByClassName(
        "BMapLib_Drawing_panel"
      )[0];
      if (BMapLib_Drawing_panel) {
        BMapLib_Drawing_panel.style.display = "block"; // 显示绘制按钮
      }
    },

    getDevlist() {
      // 获取终端列表
      var that = this;
      teamAndCarAllBindList({}).then(res => {
        if (res.status == 0) {
          that.setCarLineOptions(res); // 设置车下拉树的数据格式
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    setCarLineOptions(res) {
      // 设置车下拉树的数据格式
      let t = this,
        result = [],
        resObj = res.data;
      for (let i in resObj) {
        if (resObj[i].length > 0) {
          // 存在数组
          // resObj[i].map(x=>x.showName = `${x.licenseNo}(${x.serialNo})`);
          resObj[i].forEach(x => {
            // t.getCarImei[x.licenseNo] = x.serialNo; // 为了获取改车牌号对应的设备号
            t.getCarImei[x.licenseNo] = x.licenseNo; // 后面改成了需要车牌号而不是设备号查询
            x.title = x.serialNo;
          });
          result.push({
            // serialNo: i == "" ? "未分组" : i,// 改为showName了
            licenseNo: i == "" ? "未分组" : i,
            children: resObj[i]
          });
        }
      }
      this.carList = result;
    },
    goBack() {
      // 返回
      this.$emit("closePDial", this.isRefresh);
    },
    //初始化下拉框
    getSelect() {
      let that = this;

      allDestination({ type: -1 }).then(res => {
        if (res.status == 0) {
          if (res.data.records === null) {
            // that.networkOptions = [{ id: -1, name: "请选择" }];
            return;
          }
          that.networkOptions = res.data;
          // that.networkOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.networkOptions = [];
          }
          this.$message.error(res.msg);
        }
      });



    },
  }
};
</script>
