import request from '../../request'
//简化线
export function simplify(data) {
  return request({
    url: '/map/simplify',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}
//提交线
export function create(data) {
  return request({
    url: '/map/create',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}

//编辑线上网点
export function update(data) {
  return request({
    url: '/map/modifyCross',
    method: 'post',
    data
  })
}
